.checkbox{
    color: #ffffff;
    font-family: "pt_serifregular";
    font-size: 20px;
    line-height: 23px;
    letter-spacing: -0.4px;
    text-align: left;
    margin-left: 24px;
    position: relative;
    display: inline-block;

    &__style {
        width: 19px;
        height: 18px;
        background-color: #fff;
        border-radius: 5px;
        display: inline-block;

        &::after {
            content: "";
            display: inline-block;
            width: 19px;
            height: 18px;
            background: url("https://cdn4.iconfinder.com/data/icons/defaulticon/icons/png/256x256/check.png") no-repeat center center / contain;
            opacity: 0;
          }
    }

    &__text {
        display: inline-block;
        margin-left: 17px;
    }
    &__none {
        display: inline-block;
        width: 19px;
        height: 18px;
        position: absolute;
        top: 0;
        left: 0;
        margin: 0;
        opacity: 0;
        z-index: 1;
        cursor: pointer;
        
        &:checked + .checkbox__style::after {
          opacity: 1;
        }
      }

}

